.info {
    padding-top: 69px;
    margin-bottom: 24px;
    /*width: 1464px;*/

    /*background: url('../../images/img/Rectangle-big-gray.svg') no-repeat top center;*/
}

.info__wrapper {
    /*padding-left: 18.75%;*/
    /*padding-right: 17.3%;*/
    /*padding-bottom: 463px;*/

    min-width: 1364px;
    max-width: 1364px;
}

.info-banner {
    position: relative;

    margin: 75px 21% 0 -163px;
    width: 1482px;
    min-height: 714px;

    background-size: 1316px 714px;
    background: url('../../images/img/Rectangle 354.svg') no-repeat top left;
}

.info-banner__flex {
    padding-top: 92px;
    padding-left: 163px;
    max-width: 387px;
    /*max-height: 714px;*/

    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.info-banner__title {
    margin: 0 0 50px;
    padding: 0;

    font-weight: 700;
    font-size: 36px;
    line-height: 52px;
    color: #fff;
}

.info-banner__text {
    margin: 0;
    padding: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #ffffff;
}

.info-banner__icon {
    padding: 0;
    margin-top: 63px;
    margin-bottom: 22px;
}

.info-banner__image {
    position: absolute;
    right: -18px;
    top: 66px;
}

.info__button-link {
    color: #717171;
    transition: all .3s ease-in;
}

.info__button-link:hover {
    text-decoration: underline !important;
    text-decoration-color: #717171 !important;
}

@media (max-width: 1780px) {
    .info-banner {
        margin-left: 0;
        margin-right: 0;

        width: 1199px;
        height: 714px;
        min-height: 521px;

        /*background: url('../../images/img/Rectangle 356.svg') no-repeat top left;*/
        background-size: 100%;
    }

    .info-banner__flex {
        padding-left: 28px;
        padding-top: 63px;
    }

    .info-banner__title {
        margin-bottom: 56px;

        font-size: 32px;
        line-height: 46px;
    }

    .info-banner__text {
        font-size: 12px;
        line-height: 17px;
    }

    .info-banner__icon {
        margin-top: 29px;
        margin-bottom: 12px;
    }

    .info-banner__image {
        right: -148px;
        top: 34px;
    }
}

@media (max-width: 1599px) {
    .info-banner {
        margin: 75px 0 0;
        margin-bottom: 0;
    }
}

@media (max-width: 1300px) {
    .info {
        margin-bottom: 57px;
    }

    .info__wrapper {
        padding: 0;
        min-width: 960px;
        max-width: 960px;
    }

    .info-banner {
        width: 960px;
        height: 521px;

        background: url('../../images/img/Rectangle 354-1300.svg') no-repeat top left;
    }

    .info-banner__image {
        right: -40px;
        top: 71px;
        max-width: 615px;
    }
}

@media (max-width: 990px) {
    .info {
        padding-top: 0;
        margin-bottom: 75px;
    }

    .info__wrapper {
        min-width: 640px;
        max-width: 640px;
    }

    .info-banner {
        margin-bottom: 0;
        margin-top: 41px;

        width: 643px;
        min-height: 759px;
        max-height: 759px;

        background: url('../../images/img/Rectangle 355.svg') no-repeat;
        background-size: 643px 759px;
    }

    .info-banner__flex {
        padding-top: 320px;
        padding-left: 31px;
    }

    .info-banner__image {
        right: -26px;
        top: 2px;
    }

    .info-banner__title {
        z-index: 3;
    }

    .info-banner__text {
        z-index: 3;
    }
}

@media (max-width: 768px) {
    .info {
        position: relative;
        background: none;
        max-width: 100%;

    }

    .info-banner {
        margin-left: 0;
        margin-bottom: 0;
    }
}

@media (max-width: 739px) {
    .info {
        margin-bottom: 40px;
    }

    .info__wrapper {
        /*margin-top: 40px;*/
        padding-right: 0;
        min-width: 320px;
        max-width: 739px;
    }
}

@media (max-width: 739px) {
    .info-banner {
        width: 100%;
        min-height: 774px;
        align-items: center;

        display: flex;
        flex-direction: column;

        background: #222222 100%;
    }

    .info-banner__image {
        max-width: 455px;
        right: -5px;
        order: 1;
        position: relative;
    }

    .info-banner__flex {
        max-width: 439px;
        align-self: center;
        padding-left: 0;
        padding-top: 0;

        order: 2;
    }

    .info-banner__title {
        margin-bottom: 66px;

        font-size: 24px;
        line-height: 35px;
    }

    .info-banner__text {
        font-size: 12px;
        line-height: 17px;
    }

    .info-banner__icon {
        margin-bottom: 9px;
    }
}

@media (max-width: 524px) {
    .info-banner__flex {
        margin-top: -15px;
    }
}

@media (max-width: 468px) {
    .info__wrapper {
        margin-top: 34px;
        max-width: 468px;
        min-width: 300px;
    }

    .info-banner {
        margin-bottom: 0;
        min-height: 672px;
        max-height: 672px;
    }

    .info-banner__flex {
        /*padding-top: 255px;*/
        padding-left: 11px;
        margin-top: 23px;
    }

    .info-banner__title {
        margin-bottom: 23px;

        font-size: 18px;
        line-height: 26px;
    }

    .info-banner__text {
        max-width: 300px;

        font-size: 12px;
        line-height: 17px;
    }

    .info-banner__image {
        max-width: 321px;

        right: -4px;
        top: 19px;
    }
}
